@import "colors";

.Contact-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  user-select: text;
}

.Left-container {
  flex-grow: 2;
  background: $color-cyan;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  height: 100vh;
  width: 100%;
}

.Right-container {
    flex-grow: 1;
    width: 100%;
    background: #1B1E25;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
    align-items: center;
}

.Left-header {
  color: $color-white;
  font-size: calc(10px + 6vmin);
  font-family: 'Segoe Print', serif;
  font-weight: bold;
}

.Right-contents-container {
    width: 70%;
    border-radius: 5px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}

.Brief-container {
    align-self: center;
}

.Social-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
}

.Social-item-container {
    width: 100px;
    height: 140px
}

.Social-image {
    width: 80px;
    height: 80px;
}

.Social-image:hover {
    border-bottom: 3px solid;
}


@media only screen and (max-width: 975px) {
    .Contact-container {
        flex-direction: column;
    }
}



